<script type="text/ecmascript-6">
import Location from 'components/location/location'
import Tab from 'components/tab/tab'
import { formatDate, getWeekDay } from 'common/js/util'
import {
  /* eslint-disable no-unused-vars */
  createAPI
} from 'cube-ui'

export default {
  data () {
    return {
      beginDateTime: '',
      endDateTime: '',
      checkList: ['2'],
      options: [
        {
          label: '小',
          value: '1'
        },
        {
          label: '中',
          value: '2'
        },
        {
          label: '大',
          value: '3'
        },
        {
          label: '超大',
          value: '4'
        }

      ],
      scrollOptions: {
        scrollbar: false
      },
      advs: [
        {
          url: 'http://www.didichuxing.com/',
          image: '//webapp.didistatic.com/static/webapp/shield/cube-ui-examples-slide01.png'
        },
        {
          url: 'http://www.didichuxing.com/',
          image: '//webapp.didistatic.com/static/webapp/shield/cube-ui-examples-slide02.png'
        },
        {
          url: 'http://www.didichuxing.com/',
          image: '//webapp.didistatic.com/static/webapp/shield/cube-ui-examples-slide03.png'
        }
      ]
    }
  },
  computed: {
    beginDate () {
      return this.beginDateTime ? formatDate(this.beginDateTime, 'MM月dd日') : '--'
    },
    beginTime () {
      if (this.beginDateTime) {
        let weekDay = getWeekDay(this.beginDateTime)
        let time = formatDate(this.beginDateTime, 'hh:mm')
        return `${weekDay}${time}`
      }
      return '--'
    },
    endDate () {
      return this.endDateTime ? formatDate(this.endDateTime, 'MM月dd日') : '--'
    },
    endTime () {
      if (this.endDateTime) {
        let weekDay = getWeekDay(this.endDateTime)
        let time = formatDate(this.endDateTime, 'hh:mm')
        return `${weekDay}${time}`
      }
      return '--'
    },
    totalDays () {
      if (this.beginDateTime && this.endDateTime) {
        let delta = this.endDateTime - this.beginDateTime
        delta = delta > 0 ? delta / (1000 * 60 * 60 * 24) : 0
        return Math.ceil(delta)
      }
      return 0
    }
  },
  methods: {
    search () {
      this.$router.push({
        path: `/seller-list`
      })
    },
    selectBeginDate () {
      if (!this.beginDatePicker) {
        this.beginDatePicker = this.$createDatePicker({
          title: '开始时间',
          min: new Date(2008, 7, 8, 8, 0, 0),
          max: new Date(2020, 9, 20, 20, 59, 59),
          value: new Date(),
          format: {
            year: 'YY年',
            month: 'M月',
            date: 'D日'

          },
          columnCount: 5,
          onSelect: this.selectBeginHandle
        })
      }
      this.beginDatePicker.show()
    },
    selectEndDate () {
      if (!this.endDatePicker) {
        this.endDatePicker = this.$createDatePicker({
          title: '结束时间',
          min: new Date(2008, 7, 8, 8, 0, 0),
          max: new Date(2020, 9, 20, 20, 59, 59),
          value: new Date(),
          format: {
            year: 'YY年'
          },
          columnCount: 5,
          onSelect: this.selectEndHandle
        })
      }
      this.endDatePicker.show()
    },
    selectBeginHandle (date, selectedVal, selectedText) {
      this.beginDateTime = date
    },
    selectEndHandle (date, selectedVal, selectedText) {
      this.endDateTime = date
    }
  },
  components: {
    Tab,
    Location
  }
}
</script>

